/**
 * A function which sums two, or optionally three, numbers.
 * @customfunction MW_Get_From_Cloud
 * @param symbol {string} First number to add.
 * @returns {string[][]} uploaded data.
 * ...
 */
  function MW_Get_From_Cloud(symbol) {
  return new Promise(function (resolve, reject) {
      OfficeRuntime.storage.getItem("owner").then(function (owner) {
          OfficeRuntime.storage.getItem("source").then(function (source) {
            var url= "https://excel-endpoint.vercel.app/api/getData?symbol=" + symbol + "&source=" + source + "&owner=" + owner;
            
              var settings = {
                  "method": "GET",
                  "headers": {
                      "Content-Type": "application/json"
                  }
              }
              var max = 3;
              var count = 0;
              var callMeAgain = function(){
               fetch(url,settings).then(function (response) {

                return response.json();
              }).then(function (json) {

                console.log(json[0].timeSeries[0].price);
                var data =[];
                data.push(["asOf",json[0].asOf]);
                data.push(["name",json[0].name]);
                data.push(["owner",json[0].owner]);
                data.push(["source",json[0].source]);
                data.push(["symbol",json[0].symbol]);
                data.push(["last",json[0].last+""]);
                data.push(["timeSeries",""]);
                data.push(["Date","Price"]);
                json[0].timeSeries.forEach(function(item){
                var row =[];
                row.push(item.date);
                row.push(item.price+"");
                data.push(row);
                })
                resolve(data);
            }).catch(function (error) {  
              
              if(count++ < max)
               { callMeAgain();} 
               else
               {
                var data =[];
                data.push(["error",JSON.stringify(error)]);
                resolve(data);
               }
               
              });
            }
            callMeAgain();
          })
      })
  })
}
/**
 * A function which sums two, or optionally three, numbers.
 * @customfunction MW_Write_To_Cloud
 * @param asOf {string} First number to add.
 * @param name {string} second number to add.
 * @param symbol {string} third number to add.
 * @param Last {string} fourth number to add.
 * @param {number[][]} SelectedRange
 * ...
 */


function MW_Write_To_Cloud(asOf, name, symbol, Last, SelectedRange) {

  var rangeData = SelectedRange;
  var ExcelDateToJSDate = function (serial) {
      var utc_days = Math.floor(serial - 25569);
      var utc_value = utc_days * 86400;
      var date_info = new Date(utc_value * 1000);

      var fractional_day = serial - Math.floor(serial) + 0.0000001;

      var total_seconds = Math.floor(86400 * fractional_day);

      var seconds = total_seconds % 60;

      total_seconds -= seconds;

      var hours = Math.floor(total_seconds / (60 * 60));
      var minutes = Math.floor(total_seconds / 60) % 60;

      return new Date(date_info.getUTCFullYear(), date_info.getUTCMonth(), date_info.getUTCDate(), hours, minutes, seconds);
  }
  return new Promise(function (resolve, reject) {
      var timeSeries = [];
      OfficeRuntime.storage.getItem("owner").then(function (owner) {
          OfficeRuntime.storage.getItem("source").then(function (source) {
              SelectedRange.forEach(function (value, index) {

                  var date = ExcelDateToJSDate(value[0]);
                  var d = new Date(date);
                  startDate = d.getUTCFullYear() + "-" + ("0" + (d.getUTCMonth() + 1)).slice(-2) + "-" + ("0" + (d.getUTCDate())).slice(-2);
                  timeSeries.push({

                      date: startDate,
                      price: value[1]


                  })

              });
              var asOfDate = ExcelDateToJSDate(asOf);
              var d = new Date(asOfDate);
              asOfDate = d.getUTCFullYear() + "-" + ("0" + (d.getUTCMonth() + 1)).slice(-2) + "-" + ("0" + (d.getUTCDate())).slice(-2);
              var sheetData = {
                  timeSeries: timeSeries,
                  symbol: symbol,
                  name: name,
                  owner: owner,
                  source: source,
                  asOf: asOfDate,
                  last: Last
              };
              console.log(sheetData);
              var url="https://excel-endpoint.vercel.app/api/postData";
              var settings = {              
                  "method": "POST",
                  "headers": {
                      "Content-Type": "text/plain",
                      'Accept': 'application/json'
                  },
                  "body": JSON.stringify(sheetData)
              };
              var max = 3;
              var count = 0;
              var callMeAgain = function(){
              fetch(url,settings).then(function (response) {
                return response.json();
              }).then(function (json) {            
                if (json.success) {
                    var returnString = "Saved to Server asof " + asOfDate;
                    resolve(returnString);
                }
                else {
                   
                  resolve(JSON.stringify(json));
                }
            }).catch(function (error) {  
              if(count++ < max)
               { callMeAgain();} 
               else
               {
                resolve(JSON.stringify(error));;
               }  
              
              });
            }
            callMeAgain();
          })
      })


  });

}

